import React from "react";
import { Task } from "../../interfaces/interfaces";
import { formTypes } from "../../services/helper";
import { makeStyles, Grid } from "@material-ui/core";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import TaskForm from "./TaskForm";
import DeleteTask from "./DeleteTask";

const useStyles = makeStyles(() => ({
  card: {
    height: "100%"
  },
  cardContent: {
    textAlign: "center",
    "& > *": {
      overflow: "hidden",
      textOverflow: "ellipsis",
    }
  },
  userDetails: {
    marginTop: "10px"
  },
  cardActions: {
    justifyContent: "center"
  }
}));

const displayDate = (date: string) => {
  const l10nHE = new Intl.DateTimeFormat("he-IL");
  return l10nHE.format(new Date(date)).split(".").join("/");
};

interface Props {
  tasks: Task[] | null;
  getTasks: () => void;
  setMessage: (message: string, messageSeverity: string) => void;
}

const Tasks: React.FC<Props> = ({ tasks, getTasks, setMessage }) => {
  const classes = useStyles();

  return (
    <Grid container spacing={2}>
      {tasks && tasks.map((task: Task) => (
        <Grid item xs={6} md={5} key={task._id}>
          <Card elevation={3} key={task._id} className={classes.card}>
            <CardContent className={classes.cardContent}>
              <Typography gutterBottom variant="h5" component="h2">{task.title}</Typography>
              <Typography variant="subtitle1">{task.description}</Typography>
              <div className={classes.cardContent + " " + classes.userDetails}>
                <Typography variant="subtitle1" color="textSecondary">{task.userId?.email}</Typography>
                <Typography variant="subtitle1" color="textSecondary">Created at: {displayDate(task.createdAt)}</Typography>
              </div>
            </CardContent>
            <CardActions className={classes.cardActions}>
              <TaskForm formType={formTypes.edit} taskToEdit={task} getTasks={getTasks} setMessage={setMessage} />
              <DeleteTask task={task} getTasks={getTasks} setMessage={setMessage} />
            </CardActions>
          </Card>
        </Grid>
      ))}
    </Grid>
  );
};

export default Tasks;