import React, { useContext, useState, useEffect } from "react";
import { MessageContext } from "../../contexts/MessageContext";
import { formTypes } from "../../services/helper";
import { getTasksService } from "../../services/tasks";
import { header } from "../../styles/Styles";
import { Container, Typography } from "@material-ui/core";
import TaskForm from "./TaskForm";
import Tasks from "./Tasks";

const TasksContainer: React.FC = () => {
  const { setMessage, setMessageSeverity } = useContext(MessageContext);
  const [tasks, setTasks] = useState(null);

  useEffect(() => {
    getTasks();
    // eslint-disable-next-line
  }, []);

  const getTasks = async () => {
    const res = await getTasksService();
    const { success, data } = res;

    if (success) {
      setTasks(data);
    }
  };

  const setTaskOperationMessage = (message: string, messageSeverity: string) => {
    setMessage(message);
    setMessageSeverity(messageSeverity);
  };

  return (
    <Container>
      <Typography component="h1" variant="h5" style={header}>Tasks</Typography>
      <TaskForm getTasks={getTasks} formType={formTypes.add} setMessage={setTaskOperationMessage} />
      <Tasks tasks={tasks} getTasks={getTasks} setMessage={setTaskOperationMessage} />
    </Container>
  );
};

export default TasksContainer;