import React, { createContext, useState } from 'react';

interface Props {
  children: React.ReactNode;
}

const MessageContext: any = createContext(undefined);

const MessageContextProvider: React.FC<Props> = ({ children }) => {
  const [message, setMessage] = useState("");
  const [messageSeverity, setMessageSeverity] = useState("");

  const clearMessage = () => {
    setMessage("");
    setMessageSeverity("");
  };

  return (
    <MessageContext.Provider value={{ message, messageSeverity, setMessage, setMessageSeverity, clearMessage }}>
      {children}
    </MessageContext.Provider>
  );
};

export { MessageContext, MessageContextProvider };